* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.card-list {
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
    padding-bottom: 30px;
}

.info-container {
  text-align: left;
  margin-top: 50px;
}
